import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import CBGCaculator from './views/CBGCaculator.vue';

Vue.use(Router);

export default new Router({
  base: '/AlongFireWarmKit/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/cbg',
      name: 'cbg',
      component: CBGCaculator,
    },
  ],
});
